.clients {
	min-width: 320px;
	position: relative;
	background-color: $light-gray;
	@include r(767) {
		margin-top: 0;
		background-color: $white; }

	&__container {}

	&__background {
		position: absolute;
		width: 100%;
		height: 900px;
		background: url("../img/clients-background.png") no-repeat center;
		background-size: cover;
		@include r(1199) {
			height: 752px; }
		@include r(991) {
			height: 600px; }
		@include r(767) {
			display: none; } }

	&__wrapper {
		width: 100%;
		max-width: 1322px;
		margin: 389px auto 0;
		background-color: $white;
		@include r(1199) {
			margin-top: 315px;
			max-width: 1100px; }
		@include r(991) {
			margin-top: 239px;
			max-width: 860px; }
		@include r(767) {
			margin-top: 0px; } }


	&__title {
		margin: 88px 0 70px;
		@include r(767) {
			margin: 59px 0 0;
			text-align: center; } }

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include r(767) {
			margin-top: 40px; } }

	&__image-wrapper {
		height: 45px; }

	&__image {}

	&__text {
		@include markpro;
		font-size: 14px;
		line-height: 1.57;
		text-align: center;
		padding: 0 20px;
		margin-top: 18px;
		@include r(767) {
			padding: 0 3px;
			margin-top: 0; } }

	&__link-row {
		display: flex;
		justify-content: flex-end;
		margin-top: 72px;
		margin-bottom: -21px;
		@include r(991) {
			margin-top: 52px; }
		@include r(767) {
			margin-bottom: -7px; } }

	&__link {} }
