.advantages {

	&__container {
		margin-top: 105px;
		@include r(991) {
			margin-top: 117px; }
		@include r(767) {
			margin-top: 76px; } }

	&__title {
		text-align: center;
		margin-bottom: 40px;
		@include r(991) {
			padding: 0 140px;
			line-height: 1;
			margin-bottom: 50px; }
		@include r(767) {
			padding: 0;
			margin-bottom: 18px; } }

	&__list {}

	&__item {
		position: relative; }

	&__image {
		width: 100%;
		height: 672px;
		object-fit: cover;
		@include r(1199) {
			height: 586px; }
		@include r(991) {
			height: 450px; }
		@include r(767) {
			height: 185px; } }

	&__text {
		position: absolute;
		@include markpro;
		padding: 0 25px;
		font-weight: 700;
		font-size: 30px;
		line-height: 1.45;
		left: 50%;
		top: 50%;
		transform: translateX(-50%) translateY(-50%);
		width: 100%;
		text-align: center;
		color: $white;
		@include r(991) {
			font-size: 20px;
			padding: 0 12px; }
		@include r(767) {
			font-size: 16px; } }

	&__button-row {
		display: flex;
		justify-content: flex-end;
		margin-top: 68px;
		margin-bottom: 120px;
		@include r(767) {
			margin-top: 22px;
			margin-bottom: 50px; } }


	&__link {
		@include r(767) {
			width: 100%;
			justify-content: center; } } }
